.sidebar {
    position: sticky;
    top: 60px;
    flex: 0.2;
    border-radius: 10px;
    text-align: center;
    height: fit-content;
}

.sidebar__avatar {
    margin-bottom: 10px;
}

.sidebar__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-radius: 10px 10px 0 0;
    background-color: #fff;
}
/*  */
.sidebar__top > img {
    margin-bottom: -20px;
    width: 100%;
    height: 60px;   
    border-radius: 10px 10px 0 0;
    object-fit: cover;
}

.sidebar__top > h2 {
    font-size: 18px;
}
.sidebar__top > h4 {
    color: gray;
    font-size: 12px;
}

.sidebar__stat {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.sidebar__stat > p {
    color: gray;
    font-size: 13px;
    font-weight: 600;
}

.sidebar__stat .sidebar__statNumber {
    font-weight: bold;
    color: #0a66c2;
}

.sidebar__stats {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    background-color: #fff;
    border-radius: 0 0 10px 10px;
}

.sidebar__bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: #fff;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar__bottom > p {
    font-size: 13px;
    padding-bottom: 10px;
}

.sidebar__recentItem {
    display: flex;
    font-size: 13px;
    color: gray;
    font-weight: bolder;
    cursor: pointer;
    margin-bottom: 5px;
    padding: 5px;
}

.sidebar__recentItem:hover {
    background-color: whitesmoke;
    color: #000;
    border-radius: 10px;
}

.sidebar__hash {
    margin: 0 10px 0 5px;
}